<template>
    <div class="questionnaire">
        <h1>
            Questionnaire
        </h1>
        <question :question="questions[getIndexCurrentQuestion()]" class=""></question>
        <button @click="questionNext()" class="btn btn-primary">Suivante</button>
    </div>
</template>

<script>

    import Question from "@/components/question";
    export default {
        name: "questionnaire",
        components: {Question},
        mounted () {
          this.utilisateur.nom =  this.$route.query.nom,
          this.utilisateur.prenom = this.$route.query.prenom,
          this.utilisateur.entreprise = this.$route.query.entreprise
        },
        methods: {
            onSubmit: null,
            getIndexCurrentQuestion(){return this.currentQuestion},
            // test pour question suivante et/ou précédente 05/11/2019
            getQuestionIndex(){
                return this.currentQuestion
            },
            questionNext(){
                if(this.questions.length < this.currentQuestion +1){
                    return this.currentQuestion++
                }
            }
        },
        data() {
            return {
                currentQuestion : 0,
                questions: [
                    {
                    id: 1,
                    libelle: "quel est la meilleure waifu ?",
                    reponses: ["Zero Two", "Okazaki Nagisa"]
                },
                {
                    id: 2,
                    libelle: "Meilleur anime ?",
                    reponses: ["darling in the franxx", "kimetsu no yaiba"]
                }],
                utilisateur : {
                    nom : "",
                    prenom : "",
                    entreprise : ""
                }
            }
        }
    }

</script>

